.circleBase {
    border-radius: 50%;
    width: 10px;
    height: 10px;
    background: yellow;
    border: 2px solid rgb(195, 74, 4);
    opacity: 0.8;
}


@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(359deg);
    }
  }


  .rotate {
    animation: rotation 35s infinite linear;
  }
